.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body{
  position: relative;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

li:hover {
  background-color: grey;
  color: red;
}

#login{
  /* box-sizing: border-box; */
  position: relative;
  top: 100px;
  left: 500px; 
  height: 400px;
  width: 400px;
  display: block;
  justify-content: center;
  text-align: left;
  border: 3px solid rgb(172, 172, 183);
  border-radius: 5px;
  background-color: #a5abac;
}

 @media screen {
  #login{
    box-sizing: border-box;
    width: 100%;
    display: flex;
    left: auto;
  }
}

 @media (width>=1000px) {
  #login{
    width: 450px;
    height: 450px;
    position: absolute;
    justify-content: center;
    display: flex;
    right: calc(35%);
    top: 10rem;
  }
} 

h2{
  text-align: center;
  font-weight: bold;
}

